@import './mixins';

@property --angle {
  syntax: '<angle>';
  initial-value: 0deg;
  inherits: false;
}

#root .psm-swap-container {
  .psm-swap {
    background: #362d4a;
    padding: 30px 40px;
    margin-bottom: 15px;
    text-align: center;
    overflow-wrap: break-word;
    margin: 0px;
    position: relative;
    z-index: 0;
    color: #fff;
    border-radius: 10px;

    .psm-swap-box {
      display: flex;
      flex-direction: column;
      position: relative;

      .swap-arrow {
        margin: 24px 0;
        height: 46px;
        width: 46px;
        background-size: cover;
        background-repeat: no-repeat;
        cursor: pointer;
        background-image: url(../images/psm/icon_swap_to.svg);
        &:hover {
          background-image: url(../images/homeTokens/icon_swap_active.svg);
        }
      }

      .swap-input-box {
        .swap-input-title {
          justify-content: space-between;
          align-items: center;
          display: flex;
          flex-direction: row;

          .swap-input-title-text {
            font-weight: 700;
            font-size: 28px;
            line-height: 22px;
            color: #ffffff;
          }

          .title-info {
            display: flex;
            flex: 1;
            width: 0;
            flex-wrap: wrap;
            justify-content: flex-end;
            .input-icon-tip {
              margin-right: 6px;
            }

            .swap-balance-text {
              font-size: 14px;
              font-weight: 500;
              color: rgba(255, 255, 255, 0.6);
            }

            .swap-balance-value {
              max-width: 100%;
              font-size: 14px;
              font-weight: 600;
              color: #ffffff;
              margin-left: 6px;
              white-space: normal;
              text-align: right;
              // display: inline-flex;
              // align-items: center;

              span {
                margin-right: 5px;
              }

              img {
                margin-left: 5px;
              }
            }
          }

          .ant-select {
            color: #333;
            font-weight: bold;
            font-size: 14px;
            line-height: 16px;
            display: inline-flex;
            align-items: center;
            width: 105px;

            .ant-select-selector {
              background: transparent;
              border-radius: 10px;
              font-size: 20px;
              line-height: 24px;
              color: #ffffff;
              width: 131px;
              height: 22px;
              border: none;
              padding: 0 10px 0 0;

              .ant-select-selection-search-input {
                font-size: 16px;
              }

              .ant-select-selection-item {
                display: flex;
                align-items: center;
                padding-right: 0;
                line-height: 22px;
                justify-content: flex-start;
                margin-right: 12px;
                color: #fff;
                font-size: 16px;
                > img {
                  width: 20px;
                  height: 20px;
                  margin-right: 8px;
                }
              }
            }
            .ant-select-arrow {
              right: 10px;
              top: 50%;
            }
          }
          .ant-select-open .ant-select-arrow {
            transform: rotate(180deg);
          }
        }

        .swap-input {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 15px;
          background: #403858;
          border-radius: 10px;

          .ant-input {
            height: 64px;
            white-space: nowrap;
            // overflow: hidden;
            // text-overflow: ellipsis;
            background: transparent;
            border: none;
            padding: 14px 16px 16px 14px;
            font-weight: 700;
            font-size: 30px;
            line-height: 36px;
            color: #ffffff;
            &::placeholder {
              font-size: 30px;
              // transform: translateY(-7px);
              // -ms-transform: translateY(-7px);
              // -webkit-transform: translateY(-7px);
              color: rgba(255, 255, 255, 0.3);
            }
          }

          .swap-input-btn-max {
            margin-right: 10px;
            padding: 4px 14px;
            background: #251f32;
            border-radius: 15px;
            border: 1px solid rgba(255, 255, 255, 0.1);
            font-weight: 600;
            font-size: 12px;
            color: #fff;
            margin-left: 10px;
            white-space: nowrap;
            cursor: pointer;
          }
        }
      }

      .swap-error-tip {
        margin-top: 13px;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        min-height: 20px;
        color: #e06b63;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        &.vhide {
          visibility: hidden;
        }
      }
    }

    .swap-fee {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-top: 5px;
      font-size: 18px;
      min-height: 22px;
      img {
        margin-left: 5px;
        cursor: pointer;
      }
    }

    .submit-btn {
      background: #8c51ff;
      box-shadow: 2px 2px 8px 0px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      font-style: normal;
      display: flex;
      align-items: center;
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      width: 100%;
      height: 70px;
      justify-content: center;
      margin-top: 40px;
      cursor: pointer;

      &.disabled {
        color: rgba(255, 255, 255, 0.4);
        cursor: not-allowed;
      }
    }
  }

  .ant-modal-root {
    .psm-detail-modal.ant-modal {
      width: 900px !important;
      .ant-modal-content {
        // background: linear-gradient(180deg, #222229 0%, #18181F 100%);
        background: #222229;
        border-radius: 20px;
        color: #fff;
        .ant-modal-body {
          padding: 40px !important;
          min-height: 420px;
          .psm-amount-modal-content {
            display: flex;
            align-items: flex-start;
            flex-direction: column;
            .top {
              padding: 60px 0 36px 50px;
              text-align: left;
              width: 100%;
              background: url(../images/psm/bg-j.jpg) no-repeat;
              background-size: auto 100%;
              .tip {
                font-weight: 600;
                font-size: 20px;
                line-height: 28px;
                color: rgba(255, 255, 255, 0.6);
                display: inline-flex;
                align-items: center;
                img {
                  margin-left: 5px;
                }
              }
              .balance {
                font-weight: 700;
                font-size: 44px;
                line-height: 53px;
                padding-left: 50px;
                margin-top: 13px;
                background: url(../images/psm/psm-usdd.svg) left no-repeat;
              }
            }
            .table {
              display: flex;
              align-items: center;
              flex-direction: column;
              background: #191a1a;
              border-radius: 16px;
              width: 100%;
              // padding: 0 30px;
              .tr {
                display: inline-flex;
                align-items: center;
                padding: 30px 60px;
                justify-content: space-between;
                width: 100%;
                border-bottom: 1px dashed rgba(255, 255, 255, 0.2);
                border-radius: 16px 16px 0 0;

                &.th {
                  height: 40px;
                  line-height: 40px;
                  padding: 9px 60px;
                  border-bottom: none;

                  font-weight: 500;
                  font-size: 16px;
                  line-height: 22px;
                  color: rgba(255, 255, 255, 0.7);
                  background: rgba(0, 0, 0, 0.2);

                  .td:nth-child(2) {
                    transform: translateX(23px);
                    -ms-transform: translateX(23px);
                    -webkit-transform: translateX(23px);
                  }
                }

                &:last-child {
                  border-bottom: none;
                }

                .td {
                  text-align: right;
                  width: calc(33% - 33px);
                  font-weight: 600;
                  font-size: 16px;
                  line-height: 19px;
                  img {
                    margin-top: -3px;
                    margin-left: 7px;
                  }

                  &:first-child {
                    width: 71px;
                    text-align: left;
                  }

                  &.link {
                    color: #746bb7;
                    cursor: pointer;
                    background: url(../images/psm/regular-link.svg) 44px 2px no-repeat;
                    padding-right: 16px;
                    a {
                      color: #746bb7;
                    }

                    &:hover {
                      background: url(../images/psm/hover-link.svg) 44px 2px no-repeat;
                      a {
                        color: #998fdf;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      .ant-modal-close {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 10;
        padding: 0;
        color: rgba(0, 0, 0, 0.45);
        font-weight: 700;
        line-height: 1;
        text-decoration: none;
        background: transparent;
        border: 0;
        outline: 0;
        cursor: pointer;
        transition: color 0.3s;
        min-width: unset;
      }
    }
    .psm-contracts-modal.ant-modal {
      width: 430px !important;
      .ant-modal-close {
        width: auto;
      }
      .ant-modal-close-x {
        display: block;
        width: 56px;
        height: 56px;
        font-size: 16px;
        font-style: normal;
        line-height: 56px;
        text-align: center;
        text-transform: none;
        text-rendering: auto;
      }
      .ant-modal-content {
        padding: 0;
        background: #fff;
        border-radius: 15px;
        color: #333;
        .ant-modal-body {
          padding: 24px 0px 36px !important;
          .psm-contracts-modal-content {
            .title {
              font-weight: 500;
              font-size: 18px;
              line-height: 20px;
              margin-bottom: 20px;
              padding: 0 30px;
              text-align: center;
            }
            .content {
              padding: 0 30px;
              max-height: 420px;
              overflow: auto;
              &::-webkit-scrollbar {
                width: 6px;
                height: 6px;
                background: transparent;
              }

              &::-webkit-scrollbar-thumb {
                background: transparent;
                border-radius: 10px;
              }

              &:hover::-webkit-scrollbar-thumb {
                background: #dedede;
              }

              &:hover::-webkit-scrollbar-track {
                background: transparent;
              }
              .item {
                text-align: left;
                .item-title {
                  font-weight: 600;
                  font-size: 14px;
                  line-height: 20px;
                }
                .ul {
                  background: #f5f6ff;
                  border-radius: 5px;
                  margin-top: 10px;
                  padding: 17px 10px 17px 16px;
                  .li {
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 14px;
                    margin-top: 20px;

                    &:first-child {
                      margin-top: 0;
                    }
                    .name {
                      color: #666;
                    }
                    .address {
                      color: #5915e1;
                      margin-top: 5px;
                      display: flex;
                      align-items: center;
                      position: relative;
                      justify-content: space-between;

                      .aaa {
                        top: 0;
                        margin-top: 20px !important;
                        right: -35px;
                      }

                      a {
                        display: inline-block;
                        // width: 318px;
                        width: calc(100% - 26px);
                        background: url(../images/psm/contract-link.svg) right no-repeat;
                      }

                      img {
                        cursor: pointer;
                      }
                    }
                  }
                }
              }
              .tip {
                margin: 10px 17px 40px;
                font-weight: 500;
                font-size: 12px;
                line-height: 17px;
                text-align: left;
              }
            }
          }
        }
      }
      .ant-modal-close {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 10;
        padding: 0;
        color: rgba(0, 0, 0, 0.45);
        font-weight: 700;
        line-height: 1;
        text-decoration: none;
        background: transparent;
        border: 0;
        outline: 0;
        cursor: pointer;
        transition: color 0.3s;
        min-width: unset;
        height: unset;
      }
    }
  }

  .ant-modal-mask {
    background: rgba(0, 0, 0, 0.7);
  }
}

html body .psm-select.ant-select-dropdown {
  background: #2c2e38;
  padding: 0;
  border-radius: 10px;
  .ant-select-item {
    border-radius: 8px;
    margin: 0px;
    padding: 7px 10px;
    font-size: 20px;
    line-height: 24px;
    color: #fff;
    background: #2c2e38 !important;

    &:hover {
      background: #5915e1 !important;
    }

    .ant-select-item-option-content {
      font-size: 16px;
      line-height: 22px;
      display: flex;
      align-items: center;
      width: 100%;
      padding: 4px 0;
      img {
        width: 20px;
        height: 20px;
        margin-right: 8px;
      }
    }
  }
}

.page-psm {
  margin: 0 auto;
  max-width: 1200px;
  @include common-content-width;
  .page-psm-title {
    padding-top: 56px;
  }

  .page-psm-main {
    display: flex;
  }

  .page-psm-main-l {
    margin-right: 30px;
    flex: 1;
  }

  .page-psm-main-r {
    width: 460px;
    .psm-desc-card {
      padding: 20px;
      margin-bottom: 20px;
      border-radius: 10px;
      background: #403858 url(../images/psm/bg_intro1.png) no-repeat;
      background-position: top right;
      background-size: auto 100%;
      &:nth-child(2) {
        background-image: url(../images/psm/bg_intro2.png);
      }
      &:nth-child(3) {
        background-image: url(../images/psm/bg_intro3.png);
      }
    }
    .psm-desc-card-title {
      font-size: 20px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #ffffff;
      line-height: 28px;
    }
    .psm-desc-card-desc {
      margin-top: 16px;
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #ffffff;
      line-height: 20px;
    }

    .psm-des-jumper {
      display: flex;
      .psm-des-item {
        margin-right: 30px;
        cursor: pointer;
        &:last-child {
          margin-right: 0;
        }
      }
      .psm-des-btn {
        display: flex;
        align-items: center;
        span {
          margin-right: 10px;
          font-size: 14px;
          font-weight: 600;
          color: #ffffff;
          font-family: PingFangSC-Semibold, PingFang SC;
          line-height: 20px;
        }
        img {
          display: block;
          width: 28px;
          height: 28px;
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .page-psm {
    padding: 0 20px;
    max-width: 100%;
  }
}

@media screen and (max-width: 1079px) {
  #root {
    .psm-swap-container {
      .psm-swap {
        padding: 20px 15px;
        .psm-swap-box {
          .swap-input-box {
            .swap-input-title {
              .ant-select {
                .ant-select-selector {
                  .ant-select-selection-item {
                    margin-right: 6px;
                    font-size: 14px;
                    line-height: 20px;
                  }
                }
              }
              .title-info {
                .swap-balance-value {
                  font-size: 12px;
                  line-height: 17px;
                }
              }
            }

            .swap-input {
              margin-top: 10px;
              .ant-input {
                padding: 10px;
                height: 44px;
                font-size: 20px;
                line-height: 24px;
                &::placeholder {
                  font-size: 20px;
                }
              }
            }
          }

          .swap-arrow {
            margin: 16px 0;
            width: 30px;
            height: 30px;
            &:hover {
              background-image: url(../images/psm/icon_swap_to.svg);
            }
          }
        }
        .submit-btn {
          margin-top: 20px;
          font-size: 16px;
          line-height: 22px;
          height: 40px;
        }
      }

      .ant-modal-root
        .psm-contracts-modal.ant-modal
        .ant-modal-content
        .ant-modal-body
        .psm-contracts-modal-content
        .content {
        padding: 0 15px;
      }
    }
  }
  .page-psm {
    padding: 0 15px;
    .page-psm-title {
      padding-top: 10px;
    }
    .page-psm-main {
      display: block;
    }
    .page-psm-main-l {
      margin-right: 0;
    }
    .page-psm-main-r {
      margin-top: 30px;
      width: 100%;
      .psm-desc-card {
        margin-bottom: 14px;
        padding: 12px 10px;
        .psm-desc-card-title {
          font-size: 16px;
          line-height: 22px;
        }
        .psm-desc-card-desc {
          margin-top: 10px;
          font-size: 14px;
          line-height: 20px;
        }
      }
      .psm-des-jumper {
        flex-wrap: wrap;
        margin-top: 20px;
        .psm-des-item {
          margin-bottom: 20px;
        }
      }
    }
  }
}
